  watch: {
    cardIndex(newV) {
      if (newV < 0) {
        this.cardIndex = this.cardMax - 1;
      }
      if (newV > this.cardMax - 1) {
        this.cardIndex = 0;
      }
    },
    cardMax(newV) {
      if (this.cardIndex > newV - 1) {
        this.cardIndex = 0;
      }
    },
  },
  methods: {
    build() {
      this.cardMax = this.cardMaxTemp;
      let shuffleArray = function (array) {
        for (let i = array.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
      };
      this.cardsView = shuffleArray(this.cards);
      this.cardIndex = 1;
      this.$nextTick(() => {
        this.cardIndex = 0;
      });
      this.start = false;
      this.answer = false;
      this.answer_view = false;
      this.answer_text = "";
    },
    verify() {
      const abbreviationArray = this.cardsView
        .slice(0, this.cardMax)
        .map((card) => card.abbreviation);
      const answer = this.answer_text.trim().split("\n");

      if (answer.length > abbreviationArray.length) {
        alert(
          "Количесво карт больше допустимого. Возможно: " +
            abbreviationArray.length +
            ". Введено: " +
            answer.length
        );
      } else if (answer.length < abbreviationArray.length) {
        alert(
          "Количесво карт меньше допустимого. Возможно: " +
            abbreviationArray.length +
            ". Введено: " +
            answer.length
        );
      } else {
        this.answer_view = true;
      }
      this.answer_user = answer;
      this.answer_true = abbreviationArray;
    },
  },
  computed:{
    smile(){
      if(this.cardMax>46){
        return '))))'

      }else if(this.cardMax>40){
        return ')))'
      }else if(this.cardMax>34){
        return '))'
      }else if(this.cardMax>28){
        return ')'
      }else if(this.cardMax>22){
        return '('
      }else if(this.cardMax>16){
        return '(('
      }else if(this.cardMax>10){
        return '((('
      }
      return '(((('
    }
  },
  mounted() {
    let shuffleArray = function (array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    };
    this.cardsView = shuffleArray(this.cards);
  },
